import React, { Component } from 'react';
import { inject, } from 'mobx-react';
import { Form, Row, Col, Input, Select, Radio } from 'antd';
import './style.less';
const { Search } = Input;
const { Option } = Select;

@inject(({globalStore})=>({
  statusList: globalStore.statusList,
}))
class AdvancedSearchForm extends Component {
  state = {
    advance: false,
    keyword: ''
  };

  getComponent(item){
    switch(item.component){
      case 'input':
        return <Input placeholder={`${item.title}`} />;
      case 'select':
        return <Select placeholder={item.placeholder}>{
          item.options.map((subitem, index) =>
            <Option value={subitem.value} key={index}>{subitem.text}</Option>
          )
        }</Select>;
      default:
        return <Input placeholder={`${item.title}`} />;
    }
  }

  handleSearch = e => {
    e.preventDefault && e.preventDefault();
    const onSubmit = this.props.onSubmit;
    if(typeof onSubmit == 'function'){
      const { getFieldsValue } = this.props.form;
      let data = {}, values = getFieldsValue(),
        keyword = this.state.keyword;
      if(values){
        for(let key in getFieldsValue()){
          if(values[key]) data[key] = values[key];
        }
      }
      if(keyword) data.keyword = keyword;
      onSubmit(data);
    }
  };

  handleTypeChange = e => {
    const onTypeChange = this.props.onTypeChange;
    if(onTypeChange) onTypeChange(e.target.value)
  }

  changeKeyword = e => {
    this.setState({
      keyword: e.currentTarget.value
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const labelCol={
      xs: { span: 24 },
      sm: { span: 6 }
    };
    return (
      <Form className="ant-advanced-search-form" onSubmit={this.handleSearch} labelCol={labelCol}>
        <Row gutter={24}>
          <Col span={12}>
            <Form.Item label={``}>
              <Radio.Group defaultValue="" onChange={this.handleTypeChange}>
                <Radio.Button value=""></Radio.Button>
                <Radio.Button value="image"></Radio.Button>
                <Radio.Button value="doc"></Radio.Button>
                <Radio.Button value="video"></Radio.Button>
                <Radio.Button value="audio"></Radio.Button>
              </Radio.Group>
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label={``}>
              <Search
                placeholder={``}
                onSearch={this.handleSearch}
                onChange={this.changeKeyword}
                enterButton
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    )
  }
};
export default AdvancedSearchForm;
